html, body {
  width   : 100%;
  height  : 100%;
  margin  : 0;
  padding : 0;
  //overflow: hidden;
}

body {
  background: #151515;
}

.canvas-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width   : 100%;
  height  : 100%;
}
#renderCanvas {
  display: block;
  touch-action: none;
  width   : 100%;
  height  : 100%;
}

.bg-video-container {
  position: absolute;
  z-index: -2;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.bg-video {
  height: 100%;
  width: 100%;
  object-fit: fill;
}
.hide {
  display: none;
}

.bg-video-overlay {
  background: #000000cf;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.story-box {
  color: white;
  font-size: 24px;
  width: 300px;
  //border: 1px solid white;
  text-align: center;
  font-family: fantasy;
}

.story-box-container {
  position: absolute;
  width: 100%;
  height: 30px;
  top: 60px;
  display: flex;
  justify-content: center;
}

.password-box-container {
  position: absolute;
  width: 100%;
  height: 30px;
  bottom: 60px;
  display: flex;
  justify-content: center;

  input {
    width: 150px;
    font-size: 20px;
    font-family: fantasy;
    color: #d40707;
  }
}
